<script>
import Footer from '../components/footer/index.vue';

export default {
  data() {
    return {
      region: '選擇地區',
      city: '選擇縣市',
      cityData: {},
    };
  },
  components: {
    Footer,
  },
  methods: {
    search() {
      console.log(this.region);
      console.log(this.city);
      return this.region, this.city;
    },
  },
  computed: {
    cityFilter() {
      switch (this.region) {
        case 'North':
          return this.cityData.filter((item) => item.Region == 'North');
        case 'Central':
          return this.cityData.filter((item) => item.Region == 'Central');
        case 'South':
          return this.cityData.filter((item) => item.Region == 'South');
        case 'East':
          return this.cityData.filter((item) => item.Region == 'East');
        case 'Outer':
          return this.cityData.filter((item) => item.Region == 'Outer');
        default:
          return this.cityData;
      }
    },
  },
  mounted() {
    this.$http
      .get('City.json')
      .then((res) => {
        console.log(res);
        this.cityData = res.data;
      })
      .catch((err) => {
        console.log(err);
      });
  },
};
</script>
